.plyr__ads{
    border-radius:inherit;
    bottom:0;
    cursor:pointer;
    left:0;
    overflow:hidden;
    position:absolute;
    right:0;
    top:0;
    z-index:-1
}
.plyr__ads>div,.plyr__ads>div iframe{
    height:100%;
    position:absolute;
    width:100%
}
.plyr__ads::after{
    background:rgba(47,52,61,.8);
    border-radius:2px;
    bottom:10px;
    color:#fff;
    content:attr(data-badge-text);
    font-size:11px;
    padding:2px 6px;
    pointer-events:none;
    position:absolute;
    right:10px;
    z-index:3
}
.plyr__ads::after:empty{
    display:none
}
.plyr__cues{
    background:currentColor;
    display:block;
    height:5px;
    left:0;
    margin:-2.5px 0 0;
    opacity:.8;
    position:absolute;
    top:50%;
    width:3px;
    z-index:3
}
.plyr__preview-thumb{
    background-color:rgba(255,255,255,.9);
    border-radius:3px;
    bottom:100%;
    box-shadow:0 1px 2px rgba(0,0,0,.15);
    margin-bottom:10px;
    opacity:0;
    padding:3px;
    pointer-events:none;
    position:absolute;
    transform:translate(0,10px) scale(.8);
    transform-origin:50% 100%;
    transition:transform .2s .1s ease,opacity .2s .1s ease;
    z-index:2
}
.plyr__preview-thumb--is-shown{
    opacity:1;
    transform:translate(0,0) scale(1)
}
.plyr__preview-thumb::before{
    border-left:4px solid transparent;
    border-right:4px solid transparent;
    border-top:4px solid rgba(255,255,255,.9);
    bottom:-4px;
    content:'';
    height:0;
    left:50%;
    position:absolute;
    transform:translateX(-50%);
    width:0;
    z-index:2
}
.plyr__preview-thumb__image-container{
    background:#b7c5cd;
    border-radius:2px;
    overflow:hidden;
    position:relative;
    z-index:0
}
.plyr__preview-thumb__image-container img{
    height:100%;
    left:0;
    max-height:none;
    max-width:none;
    position:absolute;
    top:0;
    width:100%
}
.plyr__preview-thumb__time-container{
    bottom:6px;
    left:0;
    position:absolute;
    right:0;
    white-space:nowrap;
    z-index:3
}
.plyr__preview-thumb__time-container span{
    background-color:rgba(0,0,0,.55);
    border-radius:2px;
    color:#fff;
    font-size:14px;
    padding:3px 6px
}
.plyr__preview-scrubbing{
    bottom:0;
    filter:blur(1px);
    height:100%;
    left:0;
    margin:auto;
    opacity:0;
    overflow:hidden;
    position:absolute;
    right:0;
    top:0;
    transition:opacity .3s ease;
    width:100%;
    z-index:1
}
.plyr__preview-scrubbing--is-shown{
    opacity:1
}
.plyr__preview-scrubbing img{
    height:100%;
    left:0;
    max-height:none;
    max-width:none;
    -o-object-fit:contain;
    object-fit:contain;
    position:absolute;
    top:0;
    width:100%
}
.plyr--no-transition{
    transition:none!important
}
.plyr__sr-only{
    clip:rect(1px,1px,1px,1px);
    overflow:hidden;
    border:0!important;
    height:1px!important;
    padding:0!important;
    position:absolute!important;
    width:1px!important
}
.plyr [hidden]{
    display:none!important
}
